.pic-container {
    width: 80%;
    height: auto;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    .pic-wrapper {
        position: relative;
        width: 100%;
        height: auto;
        background-color: #c4c4c4;
        overflow: hidden;
        .arrow-wrapper {
            position: absolute;
            width: 4vw;
            height: 4vw;
            top: 50%;
            transform: translateY(-50%);
            z-index: 2;
            border-radius: 50% 50%;
            background: rgba($color: #555555, $alpha: 0.5);
            img {
                width: 100%;
                height: 100%;
            }
        }
        .left {
            left: 6%;
        }
        .right {
            right: 6%;
        }
        .slider {
            position: relative;
            height: 100%;
            width: 100%;
            display: flex;
            transition: transform 0.4s ease-in-out;
            transform: translateX(-100%);
            img {
                width: 100%;
                height: 100%;
                min-width: 100%;
                min-height: 100%;
            }
        }
    }
    .dot-wrapper {
        margin-top: 2%;
        position: relative;
        bottom: 0;
        display: flex;
        justify-content: center;
        height: 15px;
        width: 100%;
        left: 50%;
        transform: translateX(-50%);
        .pic-dot {
            width: 1vw;
            height: 1vw;
            border-radius: 50% 50%;
            background-color: black;
            margin: 0 0.5%;
            opacity: 0.2;
        }
    }
}
