.timeLine-container {
    position: relative;
    display: flex;
    width: 100%;
    min-width: 10rem;
    height: auto;
    justify-content: center;
    font-family: Avenir;
    font-style: normal;
    font-weight: normal;
    font-size: 1rem;
    line-height: 1rem;
    mix-blend-mode: normal;
    display: flex;
    .timeLine-icon {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        #shadow-control {
            height: 4rem;
            overflow: hidden;
            img {
                display: block;
                min-height: 100%;
                min-width: 100%;
            }
        }
        #timeline-dot {
            height: 2rem;
            width: 2rem;
        }
    }
    .timeLine-text {
        flex: 6;
        margin-left: 1rem;
        .event-date {
            margin-top: 1rem;
            font-weight: 800;
            color: #000000;
        }
        .event-info {
            margin-top: 0.5rem;
            color: #555555;
            line-height: 1.4rem;
        }
    }
}

@media screen and (max-width: 767px) {
    .timeLine-container {
        padding-left: 3rem;
        .timeLine-text {
            .event-date {
                margin-top: 1.7rem;
            }
        }
    }
}
