.nav-container {
    position: fixed;
    top: 0;
    z-index: 100;
    width: 100vw;
    height: 6rem;
    background: transparent;
    display: flex;
    justify-content: center;
    .nav-wrapper {
        position: relative;
        height: inherit;
        width: 80%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .nav-symbol {
            width: 320px;
            height: 60px;
            display: flex;
            margin-right: 3.5rem;
            align-items: center;
            justify-content: space-between;
            left: 10%;
            .nav-logo {
                width: 62px;
                height: 60px;
                margin-right: 1rem;
            }
            .nav-title {
                width: 239px;
                font-family: Comfortaa;
                font-style: normal;
                font-weight: bold;
                font-size: 1.125rem;
                color: #f26941;
            }
        }
        .nav-bar {
            width: 711px;
            min-width: 600px;
            height: 30px;
            left: 44.17%;
            display: flex;
            justify-content: space-between;
            margin-top: 5px;
            .nav-link {
                font-family: Comfortaa;
                font-style: normal;
                font-weight: 500;
                font-size: 1.125rem;
                line-height: 20px;
                color: #000000;
                text-decoration: none;
            }
            .nav-link-active {
                font-weight: bold;
                border-bottom: 2px solid #ff656a;
                color: #ff656a;
            }
        }
        .hamburger-menu-wrapper {
            flex: 1;
            display: none;
            align-items: center;
            justify-content: flex-end;
            .hamburger-menu {
                width: 2.5rem;
                height: 2.5rem;
                display: flex;
                align-items: center;
                justify-content: center;
                div {
                    width: 2rem;
                    height: 0.215rem;
                    background: #555;
                    border-radius: 3px;
                    position: relative;
                    transition: transform 0.3s;
                    z-index: 1000;
                    &:before,
                    &:after {
                        content: '';
                        position: absolute;
                        width: inherit;
                        height: inherit;
                        border-radius: 3px;
                        background: #555;
                        transition: 0.3s;
                    }
                    &:before {
                        transform: translateY(-0.5rem);
                    }
                    &:after {
                        transform: translateY(0.5rem);
                    }
                }
            }
        }
        #check {
            display: none;
            position: absolute;
            width: 2.5rem;
            height: 2.5rem;
            right: 0;
            z-index: 99990;
            cursor: pointer;
            opacity: 0;
            -webkit-tap-highlight-color: transparent;
            &:checked ~ .hamburger-menu-wrapper .hamburger-menu div {
                background: transparent;
                &:before {
                    transform: translateY(0) rotate(-45deg);
                }
                &:after {
                    transform: translateY(0) rotate(45deg);
                }
            }
        }
        .menu-items {
            display: flex;
            position: fixed;
            top: 6rem;
            left: 0;
            height: calc(100vh - 6rem);
            width: 100vw;
            margin: 0;
            flex-direction: column;
            background-color: #555555;
            justify-content: flex-start;
            transform: translateX(100%);
            padding: 4rem;
            overflow: hidden;
            .menu-link {
                margin-bottom: 40px;
                width: fit-content;
                color: #fff;
                font-weight: 800;
                font-family: Comfortaa;
                font-style: normal;
                font-weight: 500;
                font-size: 1.125rem;
                line-height: 20px;
                text-decoration: none;
                opacity: 0;
                transform: translateY(2rem);
            }
            .menu-link-active {
                border-bottom: 2px solid #fff;
                font-weight: bold;
            }
        }
    }
}
.nav-scroll {
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
    background: rgba(255, 255, 255, 0.9);
}

@keyframes animation {
    from {
        opacity: 0;
        transform: translateY(2rem);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}
@media screen and (max-width: 1100px) {
    .nav-container {
        .nav-wrapper {
            .nav-symbol {
                width: 290px;
                .nav-logo {
                    height: 50px;
                    width: 51px;
                }
            }
            .nav-bar {
                display: none;
            }
            #check {
                display: block;
                & ~ .menu-items {
                    transition: 0.3s;
                }
                &:checked ~ .menu-items {
                    display: flex;
                    transform: translateX(0);
                    .menu-link {
                        @for $i from 1 through 6 {
                            &:nth-child(#{$i}) {
                                animation: animation 0.5s ease forwards #{$i * 0.1 + 0.2}s;
                            }
                        }
                    }
                }
            }
            .hamburger-menu-wrapper {
                display: flex;
            }
        }
    }
}

@media screen and (max-width: 767px) {
    .nav-container {
        .nav-wrapper {
            .nav-symbol {
                width: 200px;
                .nav-logo {
                    height: 30px;
                    width: 31px;
                }
            }
        }
    }
}
