.footer-container {
    position: absolute;
    bottom: 0;
    width: 100vw;
    height: 40rem;
    background: transparent;
    display: flex;
    align-items: flex-end;
    overflow: hidden;
    .footer-font-normal {
        font-family: Avenir;
        font-style: normal;
        font-size: 1.125rem;
        color: #000000;
        line-height: 2.25rem;
        font-weight: 500;
    }
    .footer-icon {
        position: absolute;
        min-height: 100%;
        min-width: 100%;
        margin: 0 auto;
        object-fit: fill;
    }
    .footer-wrapper {
        width: 80%;
        height: 60%;
        position: relative;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        .contact-wrapper {
            z-index: 14;
            display: flex;
            flex-direction: column;
        }
        .qr-wrapper {
            z-index: 14;
        }
        .link-wrapper {
            z-index: 14;
            width: 250px;
            .nav-1 {
                width: fit-content;
                display: inline-block;
                margin-right: 30px;
            }
            .nav-2 {
                display: inline-block;
            }
            .footer-link {
                height: 35px;
                width: fit-content;
                border-bottom: 10px;
                display: block;
                box-sizing: border-box;
                text-decoration: none;
                color: #000000;
                &:hover {
                    border-bottom: 2px solid #000;
                }
            }
            .flex-column {
                display: flex;
                flex-direction: column;
            }
        }
        .footer-font-title {
            font-size: 1.5rem;
            margin-bottom: 2rem;
        }

        .footer-font-name {
            font-weight: 800;
        }
    }
}

@media screen and (max-width: 1100px) {
    .footer-container {
        height: 800px;
        .footer-icon {
            transform: translateX(-300px);
        }
        .footer-wrapper {
            flex-direction: column;
            height: fit-content;
            padding-bottom: 40px;
            .contact-wrapper {
                margin-bottom: 30px;
            }
            .qr-wrapper {
                margin-bottom: 30px;
                .footer-QR {
                    width: 80px;
                    height: 80px;
                }
            }
            .footer-link {
                width: fit-content;
            }
            .nav-1 {
                display: block !important;
            }
            .footer-font-title {
                margin-bottom: 1rem !important;
            }
        }
    }
}

@media screen and (max-width: 767px) {
    .footer-container {
        height: 570px;
        align-items: center;
        .footer-wrapper {
            .link-wrapper {
                .footer-link {
                    height: 2rem;
                }
            }
        }
    }
}
