.body-container {
    margin-top: 6rem;
    width: 100%;
    display: block;
    min-height: 1000px;
    padding-top: 1px;
    section {
        margin-top: 6rem;
        .center {
            margin: 0 auto;
        }
        .font {
            font-family: Avenir;
            font-style: normal;
            font-size: 1.125rem;
            font-weight: normal;
            color: #000000;
        }
        .home-title {
            text-align: center;
            font-family: Comfortaa;
            font-style: normal;
            font-weight: bold;
            font-size: 2.25rem;
            line-height: 2rem;
        }

        .section-wrapper {
            padding-top: 4rem;
            display: flex;
            width: 80%;
            min-width: 1100px;
            height: 100%;
            margin: 0 auto;
        }
    }
    #home-section1 {
        margin-top: 2rem;
    }
    #home-section2 {
        height: auto;
        .section-wrapper {
            height: 100%;
            flex-direction: column;
            display: flex;
            .lower-wrapper {
                margin-top: 3.75rem;
                display: flex;
                justify-content: space-between;
                .left-wrapper {
                    width: 45%;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    .intro {
                        color: #323130;
                        line-height: 1.5rem;
                        text-align: justify;
                    }
                    .feature {
                        height: 128px;
                        background: #f5f5ff;
                        margin-top: 1rem;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        padding: 0 30px;
                        img {
                            height: 70px;
                            width: 70px;
                        }
                        .text-wrapper {
                            width: 80%;
                            height: 100%;
                            display: flex;
                            flex-direction: column;
                            justify-content: space-around;
                            padding: 20px 0;
                            .feature-title {
                                font-weight: 800;
                                font-size: 1.5rem;
                                line-height: 1.5rem;
                            }
                            .feature-info {
                                color: #555555;
                                line-height: 1.5rem;
                            }
                        }
                    }
                }
                .right-wrapper {
                    width: 45%;
                    overflow: hidden;
                    text-align: center;
                }
            }
        }
    }
    #home-section3 {
        height: auto;
        .section-wrapper {
            display: flex;
            flex-direction: column;
            align-items: center;
            .program-title {
                margin-bottom: 3.75rem;
            }
            ul {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                justify-content: space-between;
                li {
                    display: block;
                    width: 45%;
                    list-style: none;
                    height: fit-content;
                    margin-bottom: 40px;
                }
            }
        }
    }
    #home-section4 {
        position: relative;
        width: 100%;
        height: 120rem;
        background: #fff3f4;
        .section4-wrapper {
            justify-content: space-between;
            height: 100%;
            .timeLine-wrapper {
                position: relative;
                width: 25%;
                min-width: 250px;
                .timeLine-item-wrapper {
                    margin-top: 3rem;
                    .event-item {
                        list-style: none;
                        display: flex;
                        justify-content: center;
                    }
                }
            }
            .schoolInfo-wrapper {
                position: relative;
                width: fit-content;
                display: flex;
                flex-direction: column;
                align-items: center;
                .home-title {
                    min-width: 14rem;
                }
                .infoCard-wrapper {
                    margin-top: 3rem;
                    .infoCard-item {
                        list-style: none;
                        width: 32rem;
                        height: 18rem;
                        min-width: 300px;
                        margin-bottom: 2rem;
                    }
                }
                .schedule-btn {
                    outline: 0;
                    border: 0 none;
                    height: 3rem;
                    width: 10rem;
                    background: #ff656a;
                    border-radius: 100px;
                    line-height: 100%;
                    font-size: 1rem;
                    display: flex;
                    color: #ffffff;
                    font-weight: 800;
                    justify-content: center;
                    &:hover {
                        box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
                            0 17px 50px 0 rgba(0, 0, 0, 0.19);
                    }
                    &:focus {
                        border: 0 none;
                        outline: none;
                    }
                    &:active {
                        box-shadow: 0 6px 16px 0 rgba(0, 0, 0, 0.24),
                            0 17px 50px 0 rgba(0, 0, 0, 0.19);
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 1100px) {
    html {
        font-size: 14px;
    }
    .body-container {
        section {
            .section-wrapper {
                min-width: unset;
            }
        }
        #home-section2 {
            .section-wrapper {
                .lower-wrapper {
                    justify-content: center;
                    .left-wrapper {
                        width: 70%;
                        min-width: 500px;
                        .feature {
                            .text-wrapper {
                                width: 76%;
                            }
                        }
                    }
                    .right-wrapper {
                        display: none;
                    }
                }
            }
        }
        #home-section3 {
            .section-wrapper {
                ul {
                    flex-direction: column;
                    align-items: center;
                    li {
                        width: 70%;
                        min-width: 500px;
                    }
                }
            }
        }

        #home-section4 {
            height: 210rem;
            .section4-wrapper {
                flex-direction: column;
                align-items: center;
                justify-content: flex-start;
                .schoolInfo-wrapper {
                    margin-top: 100px;
                    .infoCard-item {
                        width: 460px;
                        height: 260px;
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 767px) {
    html {
        font-size: 10px;
    }
    .body-container {
        #home-section2 {
            .section-wrapper {
                .lower-wrapper {
                    .left-wrapper {
                        min-width: 300px;
                        .feature {
                            img {
                                width: 50px;
                                height: 50px;
                            }
                            .text-wrapper {
                                width: 70%;
                            }
                        }
                    }
                }
            }
        }
        #home-section3 {
            .section-wrapper {
                ul {
                    li {
                        min-width: 300px;
                    }
                }
            }
        }
        #home-section4 {
            height: 220rem;
            .section-wrapper {
                .schoolInfo-wrapper {
                    .schedule-btn {
                        box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
                            0 17px 50px 0 rgba(0, 0, 0, 0.19);
                    }
                }
            }
        }
    }
}
